@font-face {
  font-family: Gento Dream;
  src: url("GENTO DREAM.0b4274d5.ttf") format("truetype");
}

@font-face {
  font-family: PT Mono;
  src: url("PTMono.ed55fc47.ttf") format("truetype");
}

body {
  color: #6b4226;
  background-color: #f0eedb;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  animation: 2s ease-in-out fadeIn;
  overflow-x: hidden;
}

.header {
  text-align: center;
  background-color: #f0eedb;
  padding: 20px;
  font-family: PT Mono, monospace;
  animation: 2s ease-in-out fadeIn;
}

.header-text {
  color: #b0a093;
  margin-bottom: 30px;
  font-size: 15px;
  animation: 2s ease-in-out fadeIn;
}

.main-content {
  text-align: center;
  padding: 20px;
  animation: 2s ease-in-out fadeIn;
}

.hero {
  background-color: #f0eedb;
  padding: 20px;
  animation: 2s ease-in-out fadeIn;
  position: relative;
}

.logo {
  color: #b92e2e;
  margin-bottom: 5vh;
  font-family: PT Mono, cursive;
  font-size: 10vh;
  animation: 1ms ease-in-out .1s fadeIn;
}

.image-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 5vh;
  animation: 2s ease-in-out fadeIn;
  display: flex;
  position: relative;
}

.main-photo {
  border-radius: 8px;
  width: 100%;
  max-width: 55vh;
  height: auto;
  margin-top: 10vh;
  transition: transform .3s;
  transform: rotate(-7deg);
  box-shadow: 0 4px 6px #0000001a;
}

.main-photo:hover {
  transform: rotate(-9deg)scale(1.05);
}

.after-logo {
  width: 6vh;
  height: auto;
  margin-top: -1vh;
  transition: transform .3s;
  position: absolute;
  top: 85%;
  left: 41%;
  transform: rotate(10deg);
}

.after-logo:hover {
  transform: rotate(10deg)scale(1.05);
}

.photo-caption {
  color: #b92e2e;
  margin-top: .7vh;
  margin-left: 60vh;
  font-size: 2vh;
  transition: transform .3s;
  transform: rotate(-13deg);
}

.photo-caption:hover {
  transform: rotate(-13deg)scale(1.05);
}

.title {
  color: #b92e2e;
  text-align: center;
  letter-spacing: .2em;
  padding: 7vh 0;
  font-family: Gento Dream, serif;
  font-size: 9vh;
  line-height: 1.5;
  animation: 2s ease-in-out fadeIn;
  position: relative;
}

.title span {
  transition: transform .3s;
  display: inline-block;
}

.title span:hover {
  transform: scale(1.05);
}

.text p {
  color: #030302;
  text-align: center;
  margin-bottom: 1vh;
  font-family: PT Mono, monospace;
  font-size: 1.7vh;
  line-height: .5;
  transition: transform .3s;
  animation: 2s ease-in-out fadeIn;
}

.text p:hover, .text-second p:hover, .text-future p:hover, .text-last p:hover {
  transform: scale(1.05);
}

.text-second {
  color: #030302;
  text-align: center;
  margin-bottom: 1vh;
  font-family: PT Mono, monospace;
  font-size: 1.7vh;
  line-height: .5;
  animation: 2s ease-in-out fadeIn;
}

.text-future p {
  color: #030302;
  text-align: center;
  margin-bottom: 10px;
  font-family: PT Mono, monospace;
  font-size: 1.7vh;
  line-height: .5;
  animation: 2s ease-in-out fadeIn;
}

.text-last p {
  color: #030302;
  text-align: center;
  margin-bottom: 1vh;
  font-family: PT Mono, monospace;
  font-size: 1.7vh;
  line-height: .5;
  animation: 2s ease-in-out fadeIn;
}

.double-images {
  justify-content: center;
  align-items: center;
  margin: 5% 5% 4%;
  animation: 2s ease-in-out fadeIn;
  display: flex;
  position: relative;
}

.first-photo {
  max-width: 20vh;
  transition: transform .3s;
  transform: rotate(-5deg);
}

.first-photo:hover {
  transform: rotate(-5deg)scale(1.05);
}

.second-photo {
  max-width: 12vh;
  transition: transform .3s;
  position: absolute;
  top: 50%;
  left: 53%;
  transform: rotate(28deg);
}

.second-photo:hover {
  transform: rotate(28deg)scale(1.05);
}

.gallery-images {
  justify-content: center;
  align-items: center;
  margin-bottom: 3.2vh;
  animation: 2s ease-in-out fadeIn;
  display: flex;
  position: relative;
}

.gallery-image {
  border-radius: 8px;
  max-width: 85vh;
  height: auto;
  margin: 5% 5% 3vh;
  transition: transform .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.gallery-image:hover {
  transform: scale(1.05);
}

.gallery-img {
  max-width: 24.2vh;
  margin: 4%;
  transition: transform .3s;
  position: absolute;
  top: 10%;
  left: 62%;
  transform: rotate(-7deg);
}

.gallery-img:hover {
  transform: rotate(-7deg)scale(1.05);
}

.footer-text {
  color: #b92e2e;
  margin-bottom: 2.8vh;
  font-family: PT Mono, monospace;
  font-size: 15px;
  transition: transform .3s;
  animation: 2s ease-in-out fadeIn;
}

.footer-text:hover {
  transform: scale(1.05);
}

.carousel {
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  padding-top: 10vh;
  padding-bottom: 10vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.pulse-text {
  transition: transform .3s ease-in-out;
}

.pulse-text:hover {
  animation: 1s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-1vh);
  }

  100% {
    transform: translateY(0);
  }
}

.wiggle:hover {
  animation: .5s infinite wiggle;
}

@keyframes wiggle {
  0%, 100% {
    transform: rotate(-.3deg);
  }

  50% {
    transform: rotate(.3deg);
  }
}

.color-change:hover {
  animation: 1s infinite colorChange;
}

@keyframes colorChange {
  0%, 100% {
    color: #030302;
  }

  50% {
    color: #b92e2e;
  }
}

.scale-rotate:hover {
  animation: 1s infinite scaleRotate;
}

@keyframes scaleRotate {
  0%, 100% {
    transform: scale(1)rotate(0);
  }

  50% {
    transform: scale(1.1)rotate(1deg);
  }
}

.skew:hover {
  animation: 1s infinite skew;
}

@keyframes skew {
  0%, 100% {
    transform: skew(0deg);
  }

  50% {
    transform: skew(1deg, 1deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (width <= 768px) {
  .logo {
    font-size: 3vh;
  }

  .title {
    font-size: 70px;
  }

  .text, .footer-text {
    font-size: 1vh;
  }

  .header-text {
    font-size: 1.5vh;
  }

  .text-future {
    margin-top: 15%;
    margin-bottom: 7%;
  }

  .after-logo {
    width: 4.5vh;
    left: 200px;
  }

  .gallery-image {
    width: 40vh;
  }

  .gallery-img {
    width: 10vh;
    left: 475px;
  }
}

@media (width <= 480px) {
  .logo {
    font-size: 3vh;
  }

  .title {
    font-size: 45px;
  }

  .text, .footer-text {
    font-size: 1vh;
  }

  .header-text {
    font-size: 1.5vh;
  }

  .text-future {
    margin-top: 15%;
    margin-bottom: 7%;
  }

  .after-logo {
    width: 4.5vh;
    left: 70px;
  }

  .gallery-image {
    width: 40vh;
  }

  .gallery-img {
    width: 10vh;
    left: 280px;
  }
}
